// vuex
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js'
import actions from './actions.js'

// 
Vue.use(Vuex);

//   Store
const store = new Vuex.Store({
    // 
    state: {
        // 
        topics: [
            {name: 'all', desc: ''},
            {name: 'good', desc: ''},
            {name: 'share', desc: ''},
            {name: 'ask', desc: ''},
            {name: 'job', desc: ''},
            {name: 'dev', desc: ''}
          ],
        //   
        unLoginedMoreMenus: {
            login: ''
          },
        //   
        LoginedMoreMenus: {
            create:'',
            center: '',
            msg: '',
            logout: ''
          },
        //
        isLogined: false,       
        //
        loginname:'',
        // accesstoken
        accesstoken:'',
        // id
        author_id:'',
        //   
        isShowMoreMenus: false
    },
    // 
    getters: {
      moreMenus: function(state) {
        if (state.isLogined) {
          // 
          return state.LoginedMoreMenus;
        }
        return state.unLoginedMoreMenus;
      }
    },
    mutations,
    actions
});

// 
export default store;


